import React, { Fragment } from "react";
import moment from "moment";
import Timer from "../Timer/Timer";
import nftItem_4 from "../../assets/images/parallax/ex4.png";
import { useState } from "react";
import { useContext, useEffect } from "react";
import { BlockchainContext } from "../../context/BlockchainContext";

const MintSection = ({
  item,
  searchText,
  goToDetails,
  showDetailsBtn,
  editItem,
  deleteItem,
  locale,
}) => {
  const { connectWallet } = useContext(BlockchainContext);
  const [mintAmount, setMint] = useState(1);
  let [timerCompleted, setTimerCompleted] = useState(false);
  const {
    error,
    mint,
    cost,
    totalSupply,
    setErrorMessage,
    phase,
    presalemint,
    limit,
    whitelistmint,
    currentSignerAddress,
  } = useContext(BlockchainContext);
  const [feedback, setFeedback] = useState("");
  const connectHandler = async () => {
    await connectWallet();
  };

  const mintHandler = async () => {
    if (currentSignerAddress.toString() === "") {
      await connectWallet();
    } else {
      setFeedback("Processing...");
      if (phase === 1) {
        let msg = "SALE HAS NOT STARTED YET";
        setFeedback(msg);
      } else if (phase === 3) {
        const tx = await mint(1);
        if (tx === 1) {
          setFeedback("Minting Successful");
        } else {
          setFeedback(tx);
          console.log("feedback :", tx);
        }
      } else if (phase === 2) {
        const tx = await whitelistmint(1);
        if (tx === 1) {
          setFeedback("Minting Successful");
        } else {
          setFeedback(tx);
          console.log("feedback :", tx);
        }
      }
    }
  };

  return (
    <section className="mintSection " style={{ width: "100%" }}>
      <div className="container">
        <div className="row">
          <div className="col-xl-5 col-lg-5 col-md-6 col-12 text-section">
            <div className="block-text pt-24">
              <h5 className="sub-title mb-6 mb-3"> Celebration starts now!</h5>
              <h2 className="title mb-26"> Mint your lil Thirteen here</h2>
              <p className="desc mb-43 mb-3">
                {" "}
                { timerCompleted ? 'The Public sale has started!' : 'The WL list has started minting...' }
              </p>
              {!currentSignerAddress ? (
                <a className="btn-action style-2" onClick={connectHandler}>
                  Connect Wallet
                </a>
              ) : (
                <a className="btn-action style-2" onClick={connectHandler}>
                  {currentSignerAddress &&
                    `${currentSignerAddress.slice(
                      0,
                      6
                    )}...${currentSignerAddress.slice(
                      currentSignerAddress.length - 4,
                      currentSignerAddress.length
                    )}`}
                </a>
              )}
            </div>
          </div>
          <div className="col-xl-7 col-lg-7 col-12">
            <div className="token-sale text-center">
              { !timerCompleted ? (
                <Fragment>
                  <h5 className="fs-30">WL Sale Ends In</h5>
                  <div className="countdown-container">
                    <Timer/>
                  </div>
                </Fragment>
              ) : (
                <Fragment>
                  <h5 className="fs-30">Public Sale Starts</h5>
                </Fragment>
              )}
              <div className="tf-progress-bar">
                <div className="meter orange">
                  <span style={{width:`${totalSupply / 1313 * 100}%` }} />
                </div>
                <div className="descrip">
                  <h6 className="fs-14">
                    { totalSupply }
                    <span>Minted</span>
                  </h6>
                  <h6 className="fs-16">
                    1313<span>Total</span>
                  </h6>
                </div>
              </div>
              <h6 className="fs-16">{feedback}</h6>
              <div className="button-action text-center">
                <a onClick={mintHandler} className="btn-action style-2">
                  Mint Now
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

/* React.memo detail : https://reactjs.org/docs/react-api.html#reactpurecomponent  */
export default React.memo(MintSection);
