import React from "react";
import moment from "moment";
import { CountdownCircleTimer } from "react-countdown-circle-timer";

const minuteSeconds = 60;
const hourSeconds = 3600;
const daySeconds = 86400;

const timerProps = {
  isPlaying: true,
  size: 60,
  strokeWidth: 2
};

const renderTime = (dimension, time) => {
  return (
    <div className="time-wrapper">
      <div className="time">{time}</div>
    </div>
  );
};

const getTimeSeconds = (time) => (minuteSeconds - time) | 0;
const getTimeMinutes = (time) => ((time % hourSeconds) / minuteSeconds) | 0;
const getTimeHours = (time) => ((time % daySeconds) / hourSeconds) | 0;
const getTimeDays = (time) => (time / daySeconds) | 0;

export default function Timer() {
  const startTime = moment().unix(); // use UNIX timestamp in seconds
  const endTime = moment('2022-11-05T00:00:00+02:00').unix() + (60 * 60 * 4); // use UNIX timestamp in seconds

  const remainingTime = endTime - startTime;
  const days = Math.ceil(remainingTime / daySeconds);
  const daysDuration = days * daySeconds;

  return (
      <div className="clock row">
        <div className="clock-item clock-days countdown-time-value col-md-3 col-3">
          <div className="wrap">
            <div className="inner">
              <div id="canvas_days" className="clock-canvas"/>
              <div className="number">
                <CountdownCircleTimer
                  {...timerProps}
                  colors="#FFF"
                  duration={daysDuration}
                  initialRemainingTime={remainingTime}>
                  {({ elapsedTime, color }) => (
                    <span style={{ color }}>
                      {renderTime("days", getTimeDays(daysDuration - elapsedTime))}
                    </span>
                  )}
                </CountdownCircleTimer>
              </div>
              <div className="unit">
                <p className="type-days type-time">Days</p>
              </div>
            </div>
          </div>
        </div>


        <div className="clock-item clock-hours countdown-time-value col-md-3 col-3">
          <div className="wrap">
            <div className="inner">
              <div id="canvas_hours" className="clock-canvas"/>
              <div className="number">
                <CountdownCircleTimer
                  {...timerProps}
                  colors="#FFF"
                  duration={daySeconds}
                  initialRemainingTime={remainingTime % daySeconds}
                  onComplete={(totalElapsedTime) => ({shouldRepeat: remainingTime - totalElapsedTime > hourSeconds})}>
                  {({ elapsedTime, color }) => (
                    <span style={{ color }}>
                      {renderTime("hours", getTimeHours(daySeconds - elapsedTime))}
                    </span>
                  )}
                </CountdownCircleTimer>
              </div>
              <div className="unit">
                <p className="type-hours type-time">Hours</p>
              </div>
            </div>
          </div>
        </div>

        <div className="clock-item clock-minutes countdown-time-value col-md-3 col-3">
          <div className="wrap">
            <div className="inner">
              <div id="canvas_minutes" className="clock-canvas"/>
              <div className="number">
                <CountdownCircleTimer
                  {...timerProps}
                  colors="#FFF"
                  duration={hourSeconds}
                  initialRemainingTime={remainingTime % hourSeconds}
                  onComplete={(totalElapsedTime) => ({shouldRepeat: remainingTime - totalElapsedTime > minuteSeconds})}>
                  {({ elapsedTime, color }) => (
                    <span style={{ color }}>
                      {renderTime("minutes", getTimeMinutes(hourSeconds - elapsedTime))}
                    </span>
                  )}
                </CountdownCircleTimer>
              </div>
              <div className="unit">
                <p className="type-minutes type-time">Minutes</p>
              </div>
            </div>
          </div>
        </div>

        <div className="clock-item clock-seconds countdown-time-value col-md-3 col-3">
          <div className="wrap">
            <div className="inner">
              <div id="canvas_seconds" className="clock-canvas"/>
              <div className="number">
                <CountdownCircleTimer
                  {...timerProps}
                  colors="#FFF"
                  duration={minuteSeconds}
                  initialRemainingTime={remainingTime % minuteSeconds}
                  onComplete={(totalElapsedTime) => {
                    if (remainingTime - totalElapsedTime > 0) {
                      console.log('update timer')
                    } else if (remainingTime - totalElapsedTime === 0) {
                      console.log('complete timer!')
                    }
                    return {shouldRepeat: remainingTime - totalElapsedTime > 0}
                  }}>
                  {({ elapsedTime, color }) => (
                    <span style={{ color }}>
                      {renderTime("seconds", getTimeSeconds(elapsedTime))}
                    </span>
                  )}
                </CountdownCircleTimer>
              </div>
              <div className="unit">
                <p className="type-seconds type-time">Seconds</p>
              </div>
            </div>
          </div>
        </div>
      </div>
  );
}
